<script>
export default {
  data() {
    return {
      type: "", // text video

      detailCon: {}
    }
  },

  methods: {
    /**
     * 点赞
     * detailCon.isPraise == 2 为已经点赞 退出即可
     * detailCon.isPraise == 1 未点赞，直接点赞
     */
    async support() {
      if (this.detailCon.isPraise === 2) {
        return;
      }
      try {
        let { id, type } = this.$route.query;
        let openid = this.$zero.getOpenid();

        if (type === 'text') {
          // 图片资源
          type = 'img_text';
        } else if (type === 'video') {
          // 图片资源
          type = 'img'
        }

        console.log(this.$api.course.suportArticle);
        let supportResult = await this.$api.course.suportArticle({
          id,
          type: type,
          openid: openid
        });

        console.log('点赞结果', supportResult);
        if (supportResult.statusCode === 75500) {
          return this.$zero.Toast('服务异常，请稍后重试');
        }
        if (supportResult.statusCode !== 75200) {
          return this.$zero.Toast(`点赞失败，错误码：${suportArticle.statusCode}`);
        }

        // 点赞成功
        this.detailCon.isPraise = 1;
        this.detailCon.praise++;

      } catch (e) {
        console.log('错误', e);
      }
    },

    // 获取单个文章或视频的详情
    async getDetail(id) {
      try {
        let openid = this.$zero.getOpenid();
        let detailResult = await this.$api.course.getOne({
          id: id,
          openid: openid
        });
        console.log('获取文章的详情', detailResult);

        if (detailResult.statusCode === 75500) {
          return this.$zero.Toast(`服务器繁忙，请稍后再试`);
        }
        if (detailResult.statusCode !== 75200) {
          return this.$zero.Toast(`列表信息请求失败，错误码:${articleResult.statusCode}`)
        }
        this.detailCon = detailResult.data;
      } catch (e) {
        console.log('请求详情错误', e);
      }
    }
  },

  created() {
    let { id, type } = this.$route.query;
    this.getDetail(id);
  }
}
</script>
